function feedbackSlider() {
    var swiper = new Swiper('.feedback .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 20,
        pagination: {
            el: '.feedback .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            450: {
                slidesPerView: 3,
            },
        }
    })
}

function catalogSlider() {
    var swiper = new Swiper('.catalog-item .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        pagination: {
            el: '.catalog-item .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
        navigation: {
            nextEl: '.catalog-item .swiper-button-next',
            prevEl: '.catalog-item .swiper-button-prev',
        },
    })
}

function stepSlider() {
    var swiper = new Swiper('.step__slider.swiper-container', {
        slidesPerView: 5,
        spaceBetween: 24,
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            450: {
                slidesPerView: 5,
            },
        }
    })
}

feedbackSlider()
catalogSlider()
stepSlider()
