const stepsCount = $(".step").length

$(".reserve__tab").click(function () {
    let step = +$(this).attr("data-step")
    stepTo(step)
})

$("[data-step-next]").click(function () {
    let current = +$(this).attr("data-step-next")

    if(current === 2){

        if(!$(".apart-card2").hasClass("_active")){
            return
        }
    }

    if(current < stepsCount){
        stepTo(current+1)
    }
})

$("[data-step-prev]").click(function () {
    let current = +$(this).attr("data-step-prev")

    console.log(current)
    if(current > 1){
        stepTo(current-1)
    }
})



const stepTo = (step) => {
    $(`.step`).removeClass("_active")
    $(`.reserve__tab`).removeClass("_active")
    $(`.reserve__tab[data-step="${step}"]`).removeClass("_disabled")
    $(`.reserve__tab[data-step="${step}"]`).addClass("_active")
    $(`.step[data-step="${step}"]`).addClass("_active")
}




$(".apart-card2__link").click(function () {
    $(".apart-card2__link").parents(".apart-card2").removeClass("_active")
    $(this).parents(".apart-card2").addClass("_active")
})
